export const tools = {
	filters: {
		formatMdTime(value) {
			let obj = new Date(value * 1000);
			let m = obj.getMonth();
			let d = obj.getDate();
			m = m + 1;
			if (m < 10) {
				m = '0' + m;
			}
			if (d < 10) {
				d = '0' + d;
			}
			return m + '' + d + '';
		},
		formatWeek(value) {
			let week = new Date(value * 1000).getDay();
			var weekDay = ['', '', '', '', '', '', ''];
			return weekDay[week];
		},
		formatTimes(value) {
			value = value + '';
			if (value.length < 3) {
				return value;
			}
			return value.substring(0, value.length - 3);
		}
	},
	computed: {
		formatDayNum() {
			return (leave_time, checkin_time) => {
				return parseInt((leave_time - checkin_time) / (60 * 60 * 24));
			};
		}
	},
	methods: {
		//
		navigate(e) {
			if (e.href && e.href.indexOf('http') == -1) { //					

				this.$util.uniCopy({
					content: this.vuex_config.upload.cdnurl + e.href,
					success: () => {
						this.$u.toast(',')
					}
				})




			}
		},
		//url
		copyUrl(url='') {
			this.$util.uniCopy({
				content: url || window.location.href,
				success: () => {
					this.$u.toast('');
				},
				error: () => {
					console.log('')
				}
			})
		},
		//cdnurl
		cdnurl(url) {
			if (!/^((?:[a-z]+:)?\/\/|data:image\/)(.*)/.test(url)) {
				return this.vuex_config.upload.cdnurl + url;
			};
			return url;
		},
		//
		goPage(path, auth) {
			if(path=='out'){
				this.$u.vuex('vuex_token', '');
				this.$u.vuex('vuex_user', {});
				return;
			}
			if (auth && !this.vuex_token) {
				this.$u.route('/pages/login/mobilelogin');
				return;
			}
			uni.navigateTo({
				url: path,
				complete(e) {
					console.log(e,path)
				}
			})
		},
		getDefaultStore() {
			return new Promise((resolve,reject)=>{
				this.$api.getDefaultStore({
					latitude:this.vuex_location.latitude,
					longitude:this.vuex_location.longitude
				}).then(res => {
					if (res.code == 1) {
						this.$u.vuex('vuex_store', res.data);
						resolve()
					} else {
						this.$u.toast(res.msg);
					}
				});	
			})		
		}
	}
}
//
export const avatar = {
	methods: {
		chooseAvatar() {
			uni.$on('uAvatarCropper', this.upload);
			this.$u.route({
				// ""
				url: '/uview-ui/components/u-avatar-cropper/u-avatar-cropper',
				// 
				params: {
					// px
					destWidth: 300,
					// px
					rectWidth: 300,
					// 'png'"jpg"
					fileType: 'jpg'
				}
			});
		},
		upload: async function(path) {
			uni.$off('uAvatarCropper', this.upload);
			// 
			try {
				let res = await this.$api.goUpload({
					filePath: path
				});
				if (!res.code) {
					this.$u.toast(res.msg);
				};
				this.form.avatar = res.data.url;
				this.url = res.data.fullurl;
				if (typeof this.editAvatar == 'function') {
					this.editAvatar();
				}
			} catch (e) {
				console.error(e);
				this.$u.toast('');
			}
		}
	}
}
//form
export const formRule = {
	methods: {
		//
		getRules(rule, title) {
			let arr = rule.split(';');
			let rule_arr = [];
			arr.forEach(item => {
				item = this.$u.trim(item);
				switch (item) {
					case 'required':
						rule_arr.push({
							required: true,
							message: title + '',
							// 
							trigger: ['change', 'blur']
						});
						break;
					case 'digits': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return this.$u.test.digits(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'letters': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return this.$u.test.letter(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'date': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return this.$u.test.date(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'time': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return /^(?:[01]\d|2[0-3]):[0-5]\d:[0-5]\d$/.test(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'email': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return this.$u.test.email(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'url': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return this.$u.test.url(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'qq': //qq
						rule_arr.push({
							validator: (rule, value, callback) => {
								return /^[1-9][0-9]{4,10}$/.test(value);
							},
							message: 'QQ',
							trigger: ['change', 'blur']
						});
						break;
					case 'IDcard': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return this.$u.test.idCard(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'tel': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return /^\d{3}-\d{8}$|^\d{4}-\d{7,8}$/.test(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'mobile': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return this.$u.test.mobile(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'zipcode': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return /^(0[1-7]|1[0-356]|2[0-7]|3[0-6]|4[0-7]|5[1-7]|6[1-7]|7[0-5]|8[013-6])\d{4}$/.test(value);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'chinese': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return /^(?:[\u3400-\u4DB5\u4E00-\u9FEA\uFA0E\uFA0F\uFA11\uFA13\uFA14\uFA1F\uFA21\uFA23\uFA24\uFA27-\uFA29]|[\uD840-\uD868\uD86A-\uD86C\uD86F-\uD872\uD874-\uD879][\uDC00-\uDFFF]|\uD869[\uDC00-\uDED6\uDF00-\uDFFF]|\uD86D[\uDC00-\uDF34\uDF40-\uDFFF]|\uD86E[\uDC00-\uDC1D\uDC20-\uDFFF]|\uD873[\uDC00-\uDEA1\uDEB0-\uDFFF]|\uD87A[\uDC00-\uDFE0])+$/
									.test(
										value
									);
							},
							message: '',
							trigger: ['change', 'blur']
						});
						break;
					case 'username': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								return /^[a-zA-Z0-9_]{3,12}$/.test(value);
							},
							message: '3-12',
							trigger: ['change', 'blur']
						});

						break;
					case 'password': //
						rule_arr.push({
							validator: (rule, value, callback) => {
								let val = this.$u.trim(value, 'all');
								if (val != value) {
									return false;
								}
								return /^[0-9a-zA-Z!@#$%^&*?]{6,16}$/.test(value);
							},
							message: '6-16',
							trigger: ['change', 'blur']
						});
						break;
				}
			});
			return rule_arr;
		}
	}
}

